import React from 'react';
import { Button, Icon, Label, Table } from 'semantic-ui-react';
import { getOptionText } from '../../../utils/dataUtils';
import { IFormFieldList, TOption } from '../interfaces';
import { StatusLabel } from '../shared/StatusLabel';

export const FormFieldListTable = ({
	formFieldList,
	handleDelete,
	handleEdit,
	tableOptions,
	columnOptions,
	pkField,
}: {
	formFieldList: IFormFieldList[];
	tableOptions: TOption[];
	columnOptions: TOption[];
	handleDelete: (id: number) => void;
	handleEdit: (id: number) => void;
	pkField: number | null;
}) => {
	return (
		<div>
			{formFieldList.length < 1 ? (
				<p style={{ textAlign: 'center' }}>Please Add Form Fields</p>
			) : (
				<Table size="small" compact="very" striped celled textAlign="left" selectable>
					<Table.Header>
						<Table.Row>
							<Table.HeaderCell>Name</Table.HeaderCell>
							<Table.HeaderCell>Placeholder</Table.HeaderCell>
							<Table.HeaderCell>Required</Table.HeaderCell>
							<Table.HeaderCell>ReadOnly</Table.HeaderCell>
							<Table.HeaderCell>Ebps Column</Table.HeaderCell>
							<Table.HeaderCell>Reference Table</Table.HeaderCell>
							<Table.HeaderCell>Reference Column</Table.HeaderCell>
							<Table.HeaderCell>Actions</Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						{formFieldList.map((row) => (
							<Table.Row key={row.id}>
								<Table.Cell>
									{row.name}{' '}
									{pkField === row.id && (
										<Label className={'success-label'} size="small">
											<Icon name="key" />
											Primary Key
										</Label>
									)}
								</Table.Cell>
								<Table.Cell>{row.placeholder}</Table.Cell>
								<Table.Cell>
									<StatusLabel status={row.required} mode="yes-no" />
								</Table.Cell>
								<Table.Cell>
									<StatusLabel status={row.readOnly} mode="yes-no" />
								</Table.Cell>
								<Table.Cell>{getOptionText(row.ebpsColumnId, columnOptions)}</Table.Cell>
								<Table.Cell>{getOptionText(row.referencedTableId, tableOptions)}</Table.Cell>
								<Table.Cell>{getOptionText(row.referencedColumnId, columnOptions)}</Table.Cell>
								<Table.Cell>
									<Button
										icon="edit"
										className="primary-btn"
										size="tiny"
										title="Edit"
										compact
										onClick={() => handleEdit(row.id || 0)}
									/>
									<Button
										compact
										icon="close"
										title="Remove"
										size="tiny"
										className="danger-button"
										onClick={() => handleDelete(row.id || 0)}
									/>
								</Table.Cell>
							</Table.Row>
						))}
					</Table.Body>
				</Table>
			)}
		</div>
	);
};
