import React from 'react';
import { Input } from 'semantic-ui-react';

export const InputColumnFilter = ({ column: { filterValue, setFilter } }) => {
	// Render input filter
	return (
		<Input
			size="mini"
			value={filterValue || ''}
			onChange={(e) => {
				setFilter(e.target.value);
			}}
		/>
	);
};
