import React from 'react';
import { Table } from 'semantic-ui-react';
import { floorMappingAll } from '../../../../utils/dataUtils';
import { NormalInput } from '../../../shared/fields/NormalInput';
import { TInputTypeObject, THeader } from './../utils/types/setupTypes';

type Floor = {
	floor: number;
	length: number;
	height: number;
	width: number;
	area: number;
};

type TProps = {
	floorArray: any[];
	inputType: TInputTypeObject | undefined;
	formFieldId: number;
};

export const FloorTable = ({ floorArray, inputType, formFieldId }: TProps) => {
	const headers = inputType && inputType.meta && inputType.meta.header;
	if (!headers) return null;
	return (
		<div>
			<Table collapsing size="small" compact="very" className="certificate-ui-table">
				<Table.Header>
					<Table.Row>
						<Table.HeaderCell></Table.HeaderCell>
						{Object.values(headers).map((header: THeader, idx) => {
							return (
								<React.Fragment key={`${formFieldId}.${idx}`}>
									{header.label ? <Table.HeaderCell key={header.type}>{header.label}</Table.HeaderCell> : null}
								</React.Fragment>
							);
						})}
					</Table.Row>
				</Table.Header>
				<Table.Body>
					{floorArray.map((floor: { [key: string]: number }, idx) => (
						<Table.Row key={floor.floor}>
							<Table.Cell>{floorMappingAll.find((f) => floor.floor === f.floor)!.nepaliName}</Table.Cell>
							{Object.values(headers).map((header: THeader, headerIdx) => {
								return (
									<React.Fragment key={`${formFieldId}.${idx}.${headerIdx}`}>
										{header.label ? (
											<Table.Cell key={`${formFieldId}.${idx}.${header.label}`}>
												<NormalInput label="" name={`${formFieldId}.${idx}.${header.field || header.type}`} />
											</Table.Cell>
										) : null}
									</React.Fragment>
								);
							})}
						</Table.Row>
					))}
				</Table.Body>
			</Table>
		</div>
	);
};
